/* eslint-disable import/no-extraneous-dependencies */
/** @jsx jsx */
import '@testing-library/jest-dom/extend-expect';
import { renderEditor, jsx, fireEvent, getHotkeyEvent } from '@ali/4ever-dev-test';
import {
  wait,
} from '@testing-library/react';
import { Controller, Commands } from '@ali/4ever-cangjie';
import { last } from 'lodash-es';
import { EditorWidthLegacyToolbar as Editor } from '../editor';
import { createTable } from './helpers';

describe('table render', () => {
  // 这个 case 会报错，辛苦 一辙 看下 TODO 一辙
  test.skip('表格 mount 后立刻 unmount，不应该报错', async () => {
    const value = createTable([
      ['A', 'B'],
    ]);

    const controllerRef = jest.fn();
    const { editor } = await renderEditor(<Editor defaultValue={value} controllerRef={controllerRef} />);
    jest.useFakeTimers();
    const controller = last(controllerRef.mock.calls)[0] as Controller;

    controller.command(Commands.moveToEndOfDocument);

    fireEvent.keyDown(editor, getHotkeyEvent('Backspace'));
    fireEvent.keyDown(editor, getHotkeyEvent('Backspace'));

    await wait();

    expect(() => jest.runOnlyPendingTimers()).not.toThrow();

    jest.useRealTimers();
  });
});

describe('table toolbar', () => {
  test('光标在表格内时，点击格式刷按钮正常拷贝样式', async () => {
    const value = createTable([
      ['A', 'B'],
    ]);

    const controllerRef = jest.fn();
    await renderEditor(<Editor defaultValue={value} controllerRef={controllerRef} />);
    jest.useFakeTimers();
    const controller = last(controllerRef.mock.calls)[0] as Controller;

    const toolBtn = document.querySelector('[data-testid="bi-toolbar-painter"]');
    fireEvent.click(toolBtn!);
    jest.runOnlyPendingTimers();
    await Promise.resolve();

    expect(controller.value.data.formatClipboard).not.toBe(null);

    jest.useRealTimers();
  });
});
